.card {
    font-size: 1.2em;
    width: 300px;
    background-color: rgb(255, 255, 255);
    text-align: center;
    border-radius: 20px;
    margin: 40px;
    box-shadow: 1px 1px 5px rgb(0, 0, 0);
}

.card .img{
    width: 100%;
    object-fit: cover;
    padding-top: 5px;
}

.card p {
    padding-top: 10px;
}

summary .card_img{
    width: 10px;
}

.card p:last-child{
    padding-bottom: 20px;

}

summary{
    padding-bottom: 5px;
}


@media (max-width:800px) {

    .card{
        font-size: 0.9em;
        margin: 10px;
        width: 150px;
    }
}